.prop-table {
  font-family: sans-serif; }
  .prop-table table {
    width: 100%; }
  .prop-table th {
    padding: 5px 10px; }
  .prop-table td {
    padding: 5px 10px; }
  .prop-table table, .prop-table th, .prop-table td {
    border: 1px solid #ABB5BE;
    border-collapse: collapse; }
  .prop-table tr:nth-child(even) {
    background: #FAFAFA; }
  .prop-table .story-link {
    color: #6ED2FA; }
